    p.mb-5(data-aos="fade-right") La regresi√≥n lineal se utiliza para estimar valores reales como ventas, costos, cantidad de llamadas en una central, gastos, etc., en funci√≥n de unas variables predictoras de tipo continuo. <b>La mejor ecuaci√≥n que se ajusta se llama l√≠nea de regresi√≥n y la ecuaci√≥n utilizada es:</b>

